import axios from 'axios'
import { Message, MessageBox } from 'element-ui'
import store from '../store'
import { getToken } from '@/utils/auth'

// axios
const service = axios.create({
  // baseURL: process.env.BASE_API, // apibase_url
  timeout: 15000 // 
})

// request
service.interceptors.request.use(config => {
  if (store.getters.token) {
    console.log(config)
    if (!config.params) {
      config.params = {}
    }
    config.params.access_token = getToken() // token 
  }
  return config
}, error => {
  // Do something with request error
  console.log(error) // for debug
  Promise.reject(error)
})

// respone
service.interceptors.response.use(
  response => {
    const res = response.data
    if (res.status !== 'success') {
      console.log(Message)
      Message({
        message: res.message,
        type: 'error',
        duration: 5 * 1000
      })

      // 50008:token; 50012:;  50014:Token ;
      if (res.errorCode === 50008 || res.errorCode === 50012 || res.errorCode === 50014) {
        MessageBox.confirm('', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          store.dispatch('FedLogOut').then(() => {
            location.reload() // vue-router bug
          })
        })
      }
      return Promise.reject('error')
    } else {
      return res
    }
  },
  error => {
    console.log('err' + error) // for debug
    Message({
      message: error.message,
      type: 'error',
      duration: 5 * 1000
    })
    return Promise.reject(error)
  }
)

export default service



